.button::before {
  content: "";
  position: absolute;
  top: 0.25rem;
  left: 0.25rem;
  width: 100%;
  height: 100%;
  border: 0.0625rem solid #408f95;
  z-index: -2;
  display: inline;
}

.button span {
  margin-right: 0.125rem;
  margin-left: 0.3125rem;
  z-index: -2;
  display: inline-block;
}

.button__stickarrow {
  display: inline-block;
  width: 2.5rem;
  height: 0.5rem;
  border-bottom: 0.0625rem solid #fff;
  border-right: 0.0625rem solid #fff;
  transform: skew(45deg);
  padding-bottom: 0.125rem;
  margin: 0rem 0rem 0.125rem 0rem;
}

.button::after {
  background-color: #fff;
  border: 0.0625rem solid #408f95;
  position: absolute;
  top: 0;
  left: 0;
  content: "";
  width: 100%;
  height: 100%;
  transform: scale(0, 1);
  transform-origin: left top;
  transition: 0.2s cubic-bezier(0.45, 0, 0.55, 1);
  z-index: 1;
}

.button:hover::after {
  transform: scale(1, 1);
  z-index: -1;
}

