.hamburger {
  position: absolute;
  right: 2rem;
  top: 1.6875rem;
  display: inline-block;
  width: 1.875rem;
  height: 1.875rem;
  background-color: transparent;
  cursor: pointer;
  z-index: 20;
  right: 1.125rem;
}
@media screen and (min-width: 768px) {
  .hamburger {
    display: none;
  }
}

.hamburger span {
  position: absolute;
  width: 2rem;
  height: 0.0625rem;
  background-color: white;
  transition: transform 0.3s ease;
}

.hamburger span:nth-child(1) {
  top: 0rem;
  left: 0;
}

.hamburger span:nth-child(2) {
  top: 0.75rem;
  left: 0;
}

.hamburger span:nth-child(3) {
  top: 1.5rem;
  left: 0;
}

.hamburger.active span:nth-child(1) {
  transform: translate(-0.1875rem, 0.875rem) rotate(37deg);
  width: 2.5rem;
}

.hamburger.active span:nth-child(2) {
  opacity: 0;
}

.hamburger.active span:nth-child(3) {
  transform: translate(-0.25rem, -0.625rem) rotate(-37deg);
  width: 2.5rem;
}

