  display: none; /* 初期状態では非表示 */
  height: 0; /* 初期状態では高さ0 */
  width: 100%;
  background: #408f95;
  top: 0; /* 初期状態では上に隠れている */
  position: fixed; /* 画面固定 */
  overflow: hidden; /* オーバーフローを隠す */
  transition: transform 0.5s ease, height 0.5s ease; /* transformプロパティとheightプロパティの変化を0.5秒かけて行う */
}

.nav.active {
  /* active状態 */
  z-index: 20; /* 要素を他の要素の上に配置 */
  display: block; /* 要素を表示 */
  height: 0; /* 画面の高さまで展開 */
  overflow-y: scroll;
  top: 0; /* 画面上部に表示 */
  transition: transform 0.5s ease, height 0.5s ease; /* transformプロパティとheightプロパティの変化を0.5秒かけて行う */
  transform: translate(0); /* transformプロパティによる移動 */
}
