@use "global" as *;

.campaign-page__contents{
  position: relative;
}



.about-page__main-img{
  object-fit: cover;
}
.about-page__main-img::before{
  content: "";
position: absolute;
top: 50%;
left: 50%;
transform: translate(-50%,-50%);
background-color: #408F95;
}

.about-page__main-img img{
  display: block;
width: 100vw;
object-fit: cover;
background-position: center;
  @include mq(md) {
    width: 100vw;
    height: auto;
  }
}
.about-page__inner{
  position: relative;
}

.about-page__category::before {
  content: "";
  position: absolute;
  background-image: url("../images/common/_kingyohanadai-right.svg");
  top: rem(50);
  left: 10;
  z-index: 2;
  background-size: contain;
  object-fit: cover;
  background-repeat: no-repeat;
  width: rem(120);
  height: rem(55);
  @include mq(md) {
    position: absolute;
    width: rem(301);
    height: rem(138);
    background-size: contain;
    object-fit: cover;
    background-repeat: no-repeat;
    top: rem(108);
    left: 0;
    left: rem(-80);
    z-index: -1;
  }
}
.about-page__body {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  left: 0;
  margin-top: rem(52);
  @include mq(md) {
    flex-direction: initial;
    position: relative;
    align-items: center;
    margin-top: rem(150);
    left: rem(0);
  }
}

.about-page__img-1 {
  display: none;
  @include mq(md) {
    display: block;
    position: absolute;
  aspect-ratio: 132/194;
  object-fit: cover;
  z-index: 5;
  width: 37%;
  max-width: rem(399);
    left: 0;
    top: 0;
    height: auto;
    max-width: rem(399);
  }
}
.about-page__img-1 img {
  display: block;
  max-width: 100%;
  width: 100%;
}

.about-page__img-2 img {
  display: block;
  max-width: 100%;
  width: 100%;
}

.about-page__img-2 {
  aspect-ratio: 880/581;
  object-fit: cover;
  width: 100%;
  margin-top: rem(0);

  @include mq(md) {
    margin-top: 0;
    aspect-ratio: 880/581;
    padding-top: rem(80);
    margin-left: auto;
    height: auto;
    max-width: rem(880);
  }
}
.about-page__content {
  display: block;
  font-family: $second-font-family;
  font-weight: $regular;
  color: $blue;
  max-width: rem(600);
  margin-left: 0;
  padding: 0;
  margin-top: rem(40);
  z-index: 7;
  @include mq(md) {
    position: absolute;
    display: flex;
    justify-content: space-between;
    top: rem(200);
    left: rem(200);
    right: rem(40);
    padding: 0;
    max-width: 100%;
    margin-top: 0;
  }
}
.about-page__content::after{
  display: none;
  @include mq(md) {
    content: "";
    display: block;
    position: absolute;
    background-image: url("../images/common/_kingyohanadai 2.svg");
    width: rem(301);
    height: rem(138);
    background-size: contain;
    object-fit: cover;
    background-repeat: no-repeat;
    bottom: rem(-470);
    left: calc(50vw - min(48vw , 0rem));
    z-index: 20;
  }
}


.about-page__content-titles {
  position: relative;
  font-weight: $regular;
  color: $white;
  padding-left: 0;
  padding-right: 0;
  width: 100%;
  @include mq(md) {
    color: $white;
    display: inline-block;
    font-size: rem(80);
    font-weight: $regular;
    font-family: $second-font-family;
    width: rem(400);
  }
}
.about-page__content-title {
  position: absolute;
  top: calc(50% - rem(200));
  left: calc(50% - rem(24));
  transform: translate(-50%,-50%);
  display: inline-block;
  width: 70%;
  font-size: rem(40);
  font-weight: $regular;
  line-height: 1;
  text-shadow: rem(2) rem(2) rem(4) rgba(0, 0, 0, 0.5);
  @include mq(md) {
    display: block;
    top: 0;
    left: 0;
    width: 100%;
    transform: translate(0,0);
    font-size: rem(80);
    font-weight: $regular;
    font-family: $second-font-family;
    line-height: 1.25;
    text-shadow: none;
  }
}

.about-page__texts {
  font-family: $base-font-family;
  font-weight: normal;
  color: $black;
  @include mq(md) {
    color: $white;
  }
}
.about-page__text {
  font-size: rem(16);
  max-width: rem(600);
  padding-top: 0;
  @include mq(md) {
    font-size: rem(16);
    display: inline-block;
    width: rem(400);
    z-index: 12;
    padding-top: rem(16);
  }
}